<template>
  <div>
    <h1>ÊàëÊòØ‰∏ªÈ°µ</h1>
    <button @click="login">ÁôªÂΩï</button>
    <button @click="test">ÊµãËØïJWT</button>
    <button @click="thirdLogin">Á¨¨‰∏âÊñπÁôªÂΩï</button>
    <button @click="giteeLogin">gitee ÁôªÂΩï</button>

    <div>
      Áî®Êà∑‰ø°ÊÅØÔºö<input id="secret" type="text" />
      <button @click="getVerifyCode">Ëé∑ÂèñÈ™åËØÅÁ†Å</button>
      <div id="codeWrapper"></div>
      <button @click="checkVerifyCode">Ê†°È™åÈ™åËØÅÁ†Å</button>
    </div>

    <div>
      <button @click="getSvgCode">Ëé∑ÂèñÂõæÁâáÈ™åËØÅÁ†Å</button>
      <div id="svgcodeWrapper"></div>
      <input type="text" id="svgcodeInput" />
      <button @click="checkSvgCode">Ê†°È™åÂõæÁâáÈ™åËØÅÁ†Å</button>
    </div>
  </div>
</template>

<script>
import axios from "axios";

export default {
  data() {
    return {};
  },
  methods: {
    login() {
      axios.post("/api/login", { name: "zq" }).then((res) => {
        if (res?.data?.data?.token) {
          localStorage.setItem("token", res.data.data.token);
          this.$router.push("/table");
        }
      });
    },

    test() {
      axios.get("/api/test").then((res) => {
        console.log("üöÄ ~ axios.get ~ test res:", res);
      });
    },

    thirdLogin() {
      axios.get("/api/third/login").then((res) => {
        if (res?.data?.data?.authUrl) {
          location.href = res.data.data.authUrl;
        }
      });
    },

    giteeLogin() {
      axios.get("/api/gitee/login").then((res) => {
        if (res?.data?.authUrl) {
          location.href = res.data.authUrl;
        }
      });
    },

    getVerifyCode() {
      const secret = document.getElementById("secret").value;
      const params = {
        secret,
      };
      if (!secret.trim()) return;
      axios.get("/public/getverifycode", { params }).then((res) => {
        if (res?.data?.verifyCode) {
          document.getElementById(
            "codeWrapper"
          ).innerHTML = `<P>È™åËØÅÁ†ÅÔºö<sapn id="verifyCode">${res.data.verifyCode}</span></p>`;
        }
      });
    },

    checkVerifyCode() {
      const secret = document.getElementById("secret").value;
      const verifyCode = document.getElementById("verifyCode").innerText;
      const params = {
        secret,
        verifyCode,
      };
      if (!verifyCode) return;
      axios.get("/public/checkverifycode", { params }).then((res) => {
        if (res?.data?.isValid) {
          alert("Ê†°È™åÈÄöËøá");
        } else {
          alert("Ê†°È™åÊú™ÈÄöËøá");
        }
      });
    },

    getSvgCode() {
      axios.get("/public/getsvgcode").then((res) => {
        console.log("getSvgCode", res);
        if (res?.data) {
          document.getElementById("svgcodeWrapper").innerHTML = res.data;
        }
      });
    },

    checkSvgCode() {
      const inputCode = document.getElementById("svgcodeInput").value;
      axios.post("/public/checksvgcode", { inputCode }).then((res) => {
        if (res?.data?.isValid) {
          alert("Ê†°È™åÈÄöËøá");
        } else {
          alert("Ê†°È™åÊú™ÈÄöËøá");
        }
      });
    },
  },
};
</script>
